.portfolio
{
  flex-wrap: wrap;
  width: 100%;
  padding-top: 26.5%;
  padding-bottom: 10%;
}

.portfolio__item
{
  lost-column: 1/2 2;

  position: relative;
  overflow: hidden;
  cursor: pointer;
  outline: 2px solid pink;
}

.portfolio__image
{
  transition: transform 4s linear;
}

.portfolio__description
{
  position: absolute;
  top: 0;
  left: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.portfolio__description h3,
.portfolio__description p
{
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  margin: 0;
  padding-right: .625rem; /* 10/16 */
  padding-left: .625rem; /* 10/16 */
  transition: all .5s ease;
  text-align: center;
  color: var(--gold);
  background-color: color(#000 alpha(65%));
}

.portfolio__description h3
{
  align-items: flex-end;
  padding-bottom: .3em;
  font-weight: bold;
}

.portfolio__description p
{
  padding-top: .3em;
}

@media (--m)
{
  .portfolio__item
  {
    lost-column: 1/3 3;
  }

  .portfolio__item:nth-child(even) .portfolio__description h3,
  .portfolio__item:nth-child(even) .portfolio__description p
  {
    color: var(--gold);
    background-color: color(var(--gradient1) alpha(65%));
    text-shadow: .125rem .125rem 0 #00000980; /* 2/16, 2/16 */
  }

  .portfolio__item:nth-child(odd) .portfolio__description h3,
  .portfolio__item:nth-child(odd) .portfolio__description p
  {
    color: var(--mainBlack);
    background-color: color(var(--gradient2) alpha(65%));
    text-shadow: .125rem .125rem 0 #fffff980; /* 2/16, 2/16 */
  }
}

@media (--xl)
{
  .portfolio__item
  {
    lost-column: 1/3 3;
  }
}
